	module.exports = {"data":{"wordpressPost":{"title":"Get The Hair You&#8217;ve Always Dreamed Of With These FIVE Quick Tricks","content":"<p>Gorgeous, healthy, shiny hair that&#8217;s full of bounce and swing is what we all yearn for, but sometimes it can seem an impossible dream. Most of us are dealing with split ends and roughness and breakage too. But it doesn&#8217;t have to be that way, says Roberto Tarla, creative director for Keune.</p>\n<p>In fact, Roberto says our hair fantasies can (indeed will) become reality if we do the following:</p>\n<p><strong>Avoid over-washing</strong></p>\n<p>“Washing your hair with shampoo daily strips your hair of its natural oils which it needs to remain healthy and nourished.” Instead, try to stick to three, perhaps four, washes a week to allow your hair to produce its natural oils.</p>\n<p><strong>But shampoo twice</strong></p>\n<p>“The first shampoo acts as a primer and washes away all the excess product that’s left in your hair,&#8221; explains Robert.</p>\n<p>&#8220;The second shampoo allows the product to clean your hair and scalp”.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_3384\" style=\"width: 232px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3384\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07202642/JPG-Olivia-Hair-232x300.jpg\" alt=\"\" width=\"232\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07202642/JPG-Olivia-Hair-232x300.jpg 232w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07202642/JPG-Olivia-Hair.jpg 463w\" sizes=\"(max-width: 232px) 100vw, 232px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@Oliviapalermo</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>Condition with care</strong></p>\n<p>Only condition from the mid-lengths to the ends, says Robert. Conditioner moisturises the hair and detangles it after shampoo, but it&#8217;s not needed at the roots.</p>\n<p>Apply it where hair is most dry only. Doing so will smooth the hair cuticle, so hair will feel softer and appear shinier.</p>\n<p><strong>Treat hair well</strong></p>\n<p>If you truly want your hair to be your crowning glory, then a weekly intensive treatment is a must, says Robert, who likes <a href=\"https://www.salonstyle.com.au/Keune-Care-Satin-Oil-Treatment-95ml?search=Keune%20Satin%20Oil%20Intensive\">Keune Care Satin Oil Treatment, $39.95.</a></p>\n<img class=\"alignnone size-medium wp-image-3387\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07222305/JPG-Hair-Oil-163x300.jpg\" alt=\"\" width=\"163\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07222305/JPG-Hair-Oil-163x300.jpg 163w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/07222305/JPG-Hair-Oil.jpg 234w\" sizes=\"(max-width: 163px) 100vw, 163px\" />\n<p>&nbsp;</p>\n<p>Apply the treatment to shampooed, towel-dried hair, focusing on mid-lengths to ends, wrap your hair in a warm towel and wait for the treatment to nourish your hair before rinsing out with cold water.</p>\n<p>&#8220;You can also add a couple of drops of oil to the treatment before application for an extra intensive, shine-boosting treatment,&#8221; adds Robert.</p>\n<p>&nbsp;</p>\n<p><strong>Protect from heat</strong></p>\n<p>Before styling your hair, always apply a heat protector! Roberto says “this will add a protective layer to avoid further damage to the hair from blow-dryers or heated styling tools”.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/13234918/MO_Marchesa_9.13.2017__0175.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"c2d75524-1a87-50d4-82c3-58bec8c1630f"}}

